/*
Basic input element using psuedo classes
*/

html, body {
  height: 100%;
}

h1 {
  font-size: 28px;
  margin-bottom: 2.5%;
}

.col input, .col span, .col label, .col textarea {
  font-family: 'Ubuntu', sans-serif;
  display: block;
  padding: 0px;
  border: none;
  font-size: 2px;
}

.col textarea:focus, .col input:focus {
  outline: 0;
}

/* Question */

.col input.question, .col textarea.question {
  font-size: 40px;
  font-weight: 300;
  border-radius: 2px;
  margin: 0;
  border: none;
  width: 80%;
  background: rgba(0, 0, 0, 0);
  transition: padding-top 0.2s ease, margin-top 0.2s ease;
  overflow-x: hidden;
  /* Hack to make "rows" attribute apply in Firefox. */
}

/* Underline and Placeholder */

.col input.question+label, .col textarea.question+label {
  display: block;
  position: relative;
  white-space: nowrap;
  padding: 0;
  margin: 0;
  width: 10%;
  border-top: 1px solid red;
  transition: width 0.4s ease;
  height: 0px;
}

.col input.question:focus+label, .col textarea.question:focus+label {
  width: 80%;
}

.col input.question:focus, .col input.question:valid {
  padding-top: 35px;
}

.col textarea.question:valid, .col textarea.question:focus {
  margin-top: 3px;
}

.col input.question:focus+label>span, .col input.question:valid+label>span {
  top: -100px;
  font-size: 22px;
  color: #333;
}

.col textarea.question:focus+label>span, .col textarea.question:valid+label>span {
  top: -150px;
  font-size: 22px;
  color: #333;
}

.col input.question:valid+label, .col textarea.question:valid+label {
  border-color: green;
}

.col input.question:invalid, .col textarea.question:invalid {
  box-shadow: none;
}

.col input.question+label>span, .col textarea.question+label>span {
  font-weight: 300;
  margin: 0;
  position: absolute;
  color: #8F8F8F;
  font-size: 48px;
  top: -66px;
  left: 0px;
  z-index: -1;
  transition: top 0.2s ease, font-size 0.2s ease, color 0.2s ease;
}

.col input[type="submit"] {
  transition: opacity 0.2s ease, background 0.2s ease;
  display: block;
  opacity: 0;
  margin: 10px 0 0 0;
  padding: 10px;
  cursor: pointer;
}

.col input[type="submit"]:hover {
  background: #EEE;
}

.col input[type="submit"]:active {
  background: #999;
}

.col input.question:valid~input[type="submit"], .col textarea.question:valid~input[type="submit"] {
  -webkit-animation: appear 1s forwards;
  animation: appear 1s forwards;
}

.col input.question:invalid~input[type="submit"], .col textarea.question:invalid~input[type="submit"] {
  display: none;
}

@-webkit-keyframes appear {
  100% {
    opacity: 1;
  }
}

@keyframes appear {
  100% {
    opacity: 1;
  }
}

