.menu__link {
  padding: 2rem 1.5rem;
  font-weight: 500;
  color: #241c69;
  text-decoration: none;
  text-transform: uppercase;
  font-size: 16px;
}

/* states */

.menu__link:focus {
  outline: 1px solid white;
}

/* hover animation */

.menu__link {
  position: relative;
  overflow: hidden;
  will-change: color;
  transition: color .25s ease-out;
}

.menu__link::before, .menu__link::after {
  content: "";
  width: 0;
  height: 3px;
  background-color: #241c69;
  will-change: width;
  transition: width .1s ease-out;
  position: absolute;
  bottom: 0;
}

.menu__link::before {
  left: 50%;
  transform: translateX(-50%);
}

.menu__link::after {
  right: 50%;
  transform: translateX(50%);
}

.menu__link:hover::before, .menu__link:hover::after {
  width: 100%;
  transition-duration: .2s;
}

